

/* Variable CSS - Margin */
:root
{
    --mb-1: 0.5rem;
    --mb-2: 1rem;
    --mb-3: 1.5rem;
    --mb-4: 2rem;
    --mb-5: 2.5rem;
    --mb-6: 3rem;
}


* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	height: 100vh;
	font-family: "Bridge Head Con Medium";
}

::-webkit-scrollbar {
	display: none;		
}

.menu {
	position: fixed;
	width: 100%;
	height: 100vh;
	display: flex;
}

.hr {
	position: fixed;
	left: 50%;
	width: 3px;
	height: 100vh;
	background-color: #000;
	z-index: 1;
	transform: scaleY(0);
}

.menu-left,
.menu-right {
	position: fixed;
	width: 50%;
	height: 100vh;
	background: rgb(167, 28, 28);
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10;
}

.menu-left {
	left: -50%;
	z-index: 10;
}

.menu-right {
	right: -50%;
	z-index: 10;
}

.menu-link a {
	text-decoration: none;
	color: #fff;
	font-size: 100px;
}

.menu-link:first-child a::before {
	display: block;
	position: relative;
	content: "";
	background-image: url("/images/brush-stroke.png");
	background-size: 100%;
	background-repeat: no-repeat;
	top: 80px;
	left: 0;
	height: 40px;
	width: 240px;
}

.menu-link a span {
	font-family: "Poppins";
	font-size: 24px;
}

.contact .title {
	font-size: 80px;
}

.contact .info {
	font-family: Helvetica, sans-serif;
	font-weight: lighter;
}

.mail {
	margin-bottom: 60px;
}

.menu-close {
	z-index: 2;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	cursor: pointer;
	opacity: 1;
}

.menu-close-img {
	width: 140px;
	height: 140px;
	border-radius: 100%;
	border: 10px solid #000;
}

img {
	width: 100%;
}

.menu-open {
	position: fixed;
	top: 0;
	right: 0;
	padding: 2rem;
	cursor: pointer;
	font-size: 20px;
	color: #000;
}


/* overlay loader */

.overlay {
	z-index: 100;
	position: fixed;
	width: 100%;
	height: 100%;
	background: #a71c1c;
	top: 0%;
	color: #fafafa;
  }
  
  .overlay h1 {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 150px;
	font-weight: bolder;
	letter-spacing: 20px;
  }

/* Media Queries */

@media (max-width: 900px) {
	.menu-right {
		display: none;
	}

	.menu-left {
		width: 100%;
	}

	.menu-close {
		position: fixed;
		top: 10%;
		left: 80%;
		transform: translate(-50%, -50%);
		margin: 40px;
	}

	.hr {
		display: none;
	}

	img {
		width: 40%;
	}
}